   let dateParams = {
    LookupTypeId: 74
   }

   this.lookupService.getLookupValueByLookupTypeId(dateParams).subscribe((res:any) => {
    this.invoice.dueDate = moment().add(parseInt(res[0].lookupValueName), 'days');
   })

   let vatListParams = {
    LookupTypeId: 77
   }

   //VAT types
   this.lookupService.getLookupValueByLookupTypeId(vatListParams).subscribe((res:any) => {
    this.vatTypeDataList = res;
     _.each(this.vatTypeDataList, item => {
       item.custVatTypeAmount = 0;
     });

  });

  let disListParams = {
    LookupTypeId: 88
  }

  //discount types
  this.lookupService.getLookupValueByLookupTypeId(disListParams).subscribe((res:any) => {
    this.discountFinalTypeDataList = res;
  });

  //for post single invoice
  if(this.route.params['value'].type == 'single' && this.route.params['value'].invoiceid == undefined){
    this.invoiceVendorAccountsData.splice(0,1);
    this.isSingleInvoice = true;
    this.isEditInvoice = false;
  }

  //for post multi invoice
  if(this.route.params['value'].type != 'single' && this.route.params['value'].invoiceid == undefined){
   this.isSingleInvoice = false;
   this.isEditInvoice = false;
  }

  if(this.route.params['value'].invoiceid != undefined){
    
    this.isInvoiceSubmitted = false;
    this.isFormInValid = false;
    this.isEditInvoice = true;
    this.invoice.vendorId = this.route.params['value'].id;



  }

  let vendorParams = {
    apartmentId: parseInt(this.cookieService.get('apartmentId'))
  }

  this.vendorService.getVendorByApartmentId(vendorParams).subscribe((res:any) => {

    this.vendorDataList = res.filter(item => {
      return item.vendorId == this.route.params['value'].id;
    });

    this.isVendorDataLoaded = true;

  });


